















import ut from '../../utils/index.js';
import login from '../../components/login.vue'
export default {
	data() {
		return {
			check:false,
			codemsg:"获取验证码",
			userinf:{
				"phone": "",//手机号
				"code": ""//验证码
			}
		}
	},
	components: {
		login
	},
	onLoad() {
	},
	methods: {
		go_mine_login(){
			wx.redirectTo({
				url: '../mine/login'
			})
		},
		cg_active(){
			console.log(1)
			this.check=!this.check;
		},
		req_register(){		
			if(!ut.checkmobile(this.userinf.phone)){
				ut.totast('请输入正确的手机号');
				return;
			}
			if(!this.userinf.code){
				ut.totast('请输入验证码');
				return;
			}	
			ut.request({
				data: this.userinf,
				url: "user/updatephone"
			}).then(data=>{
				ut.totast('更换手机号成功');
				setTimeout(function(){
					wx.navigateBack()
				},1000)
			})
		},
		req_sendCode(){
			if(this.codemsg!="获取验证码"&&this.codemsg!="重新获取")return;
			if(!this.userinf.phone){
				ut.totast('请输入正确的手机号');
				return;
			}
			let codenum=120;
			let timer=setInterval(()=>{
				codenum--;
				this.codemsg=codenum+'s后重新获取';
				if(codenum==0){
					clearInterval(timer);
					this.codemsg="重新获取";
				}
			},1000)
			ut.request({
				data: {
					phone:this.userinf.phone
				},
				url: "user/sendCode"
			}).then(data=>{
			}).catch(data=>{
				clearInterval(timer);
				this.codemsg="重新获取";
			})
		}
	}
}
